.container {
  display: flex;
  flex-direction: row;
  padding: 10px;
  justify-content: space-around;
}

.Rchart {
  display: flex;
  flex: 1;
}

.item {
  padding: 10px;
}
.column {
    float: left;
    width: 33.33%;
    padding: 10px;
    height: 300px; 
    background-color: white;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both;
    background-color: white;
  }
  img{
    max-width: 100%;
    max-height: 100%;
  }
  .p{
    background-color: white;
  }